
.header{
    width:100vw;
    padding: 20px 10%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(198, 78, 253);
    font-family: 'Indie Flower', cursive;
    font-weight: bold;
    color:black;
}
.header label{
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    margin-left: 20px;
    display: -webkit-flex;
    display: flex;
    font-size: 1.3rem;
    margin-bottom: 5px;
}
.header label input, .header label select{
    font-size: .9rem;
    margin-left: 5px;
}
.header button{
    cursor: pointer;
    font-size: 1.2rem;
    padding: 5px 10px;
    border-radius: 5px;
    border:2px solid black;
    background-color: white;
    font-size: 1.3rem;
    font-family: 'Dokdo', cursive;
    margin-left: 15px;
}



.foodsList{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.item{
    padding: 20px 20px;
    border-radius: 5px;
    margin: 15px 10px 10px 10px; 
    border: 4px solid rgb(52, 245, 26);
    -webkit-flex-basis: 25%;
            flex-basis: 25%;
    font-size: 1.2rem;
    background-color: rgb(61, 61, 61);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-family: 'Indie Flower', cursive;
    font-weight: bold;
}
.item button:nth-of-type(1){
    cursor: pointer;
    padding: 5px;
    font-size: .9rem;
    text-shadow: 0 0 1px white;
    background-color: rgb(22, 22, 22);
    color:white;
    border-radius: 5px;
    border: 2px solid white;
    font-family: 'Dokodo', cursive;
    margin-left: 5px;
}
.breakfast{
    border-color: rgb(255, 166, 0);
}
.dinner {
    border-color: rgb(0, 204, 255);
}
.supper{
    border-color:rgb(240, 58, 240);
}
.snack{
    border-color: rgb(240, 35, 52);
}

.filter{
    padding: 10px;  
    font-family: 'Dokdo', cursive;
    font-size: 1.2rem;
}
.filter select{
    background-color: rgb(58, 57, 57);
    color:white;
    font-size: .9rem;
    font-family: 'Indie Flower', cursive;
    font-weight: bold;
    margin-left: 5px;
}
.filter select option{
    font-weight: bold;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  font-size: 18px;
}
.App{
  background-color: rgb(31, 31, 31);
  min-height: 100vh;
  color:white;
  text-shadow: 0 0 1px white;
}

@media (min-width: 1281px) {
  
  html{
    font-size: 22px;
  }
  
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: portrait){
  
  html{
    font-size: 18px;
  }
  .header{
    padding: 10px 20px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .header label{
    -webkit-flex-basis: 40%;
            flex-basis: 40%;
  }
  .item{
    -webkit-flex-basis: 40%;
            flex-basis: 40%;
    padding: 15px;
    margin-top:10px;
  }
  
}

/* 
  ##Device = Tablets, Ipads (landscape)
  ##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  html{
    font-size: 16px;
  }
  
}

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {
  
  html{
    font-size: 16px;
  }
  .header{
    padding: 10px 20px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .header label{
    -webkit-flex-basis: 40%;
            flex-basis: 40%;
  }
  .item{
    -webkit-flex-basis: 40%;
            flex-basis: 40%;
    padding: 15px;
    margin-top:10px;
  }
  
}

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
  
  html{
    font-size: 14px;
  }
  .header{
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .item{
    -webkit-flex-basis: 60%;
            flex-basis: 60%;
    padding: 15px;
    margin-top:10px;
  }
  
}


